.grid {
  margin: 20px 10px 0 10px;
  padding: 0;
}

.col {
  padding: 0 10px 0 10px;
}

.avatar {
  width: 80px;
  height: 80px;
}

.fullName {
  margin-bottom: 3px;
  font-size: 18px;
  font-weight: 700;
}

.email {
  margin: 0px;
  font-size: 14px;
  color: var(--ion-color-step-400, #BDBDBD);
}

.editBtn {
  position: relative;
  top: 18px;
  --background: var(--ion-color-step-200, #F2F2F2);
}

.divider {
  margin-bottom: 0 !important;
}

.item {
  padding: 6px 0 6px 0;
}
