.form {
  padding-top: 50px !important;
}

.item {
  margin: 15px 20px 30px 20px;
}

.select {
  width: 100%;
}

.oddOrderOption {
  margin-right: 0;
  margin-left: 0;
  width: 48%;
}

.evenOrderOption {
  margin-right: 0;
  margin-left: 0;
  float: right;
  width: 48%;
}

.circle {
  margin-left: 20px;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  background: var(--ion-color-primary);
  color: var(--ion-color-primary-contrast);
  padding-top: 5px;
  padding-right: 0.5px;
  text-align: center;
  font-size: 12px;
  font-weight: bold;
}
