.grid {
  margin: 0;
  padding: 0;
  width: 100%;
}

.col {
  margin: 0;
  padding: 0;
}

.date{
  padding: 16px 20px 16px 20px;
  border: 1px solid var(--ion-color-step-200, #E0E0E0);
  border-radius: var(--dw-border-radius-4);
  width: 100%;
  /*     */
  justify-content: left;
}

/* CSS shadow-part */
.date::part(native){
  background-color: var(--ion-background-color) !important;
  padding: 0px;
}

.unit {
  max-width: 100%;
  margin-left: 5px;
  --padding-start: 20px;
  --padding-end: 20px;
  --padding-top: 16px;
  --padding-bottom: 16px;
  border: 1px solid var(--ion-color-step-200, #E0E0E0);
  border-radius: var(--dw-border-radius-4);
}

/*     */
ion-popover::part(arrow) {
  left: calc(230px + var(--offset-x, 0));
}
