.div {
  margin-top: 30px;
}

.fieldDiv {
  /*    float    . */
  height: 36px;
  margin: 0 20px 0px 20px;
}

.field {
  color: var(--ion-text-color) !important;
  font-weight: bold;
  font-size: 14px;
}

.fieldIosFont {
  font-size: 14px !important;
  margin-bottom: 8px;
}

.addButton {
  float: right;
  position: relative;
  bottom: 12px;
  height: 28px;
  --background: var(--ion-color-step-100, #F2F2F2);
}

.item {
  --padding-top: 6px;
  --padding-bottom: 6px;
  --padding-start: 20px;
  --padding-end: 20px;
  border: 1px solid var(--ion-color-step-200, #E0E0E0);
  border-radius: var(--dw-border-radius-4);
  margin-bottom: 10px;
}


.deleteButton {
  --padding-start: 1px;
  --padding-end: 1px;
  --padding-top: 1px;
  --padding-bottom: 1px;
  --box-shadow: none;
  position: relative;
  top: 13px;
  width: 18px;
  height: 18px;
}

.record {
  position: absolute;
  right: 40px;
}