.sentry-error-embed .form-field input {
  border: 1px solid #ccc;
  border-radius: var(--dw-border-radius-4) !important;
}

.sentry-error-embed .form-field textarea {
  border: 1px solid #ccc;
  border-radius: var(--dw-border-radius-4) !important;
}

.sentry-error-embed .btn {
  margin: 5px 0 5px 0 !important;
  border: none !important;
  border-radius: var(--dw-border-radius-4);
  width: 100%;
  height: 48px;
  background-color: var(--ion-color-primary) !important;
}

.sentry-error-embed .close {
  margin: 5px 0 5px 0 !important;
  border: none !important;
  border-radius: var(--dw-border-radius-4);
  width: 100%;
  height: 48px;
  background-color: var(--ion-color-light) !important;
}