    &.mbsc-error-message-has-icon-right.mbsc-rtl {
      right: 2.66667em;
    }


    /* Underline input --------------------------------------------------------------------------- */

    /* Form element */

    &.mbsc-textfield-underline-stacked,
    &.mbsc-textfield-underline-floating {
      height: 3em;
      padding-top: .875em;
    }

    /* Icon */

    &.mbsc-textfield-icon-underline {
      top: .25em;
    }

    &.mbsc-textfield-icon-underline.mbsc-textfield-icon-floating,
