@if ($mbsc-mobiscroll-theme) {
  .mbsc-mobiscroll {
    &.mbsc-icon-button {
      border-radius: 4em;
    }

    &.mbsc-button {
      font-size: 1em;
      margin: .5em;
      padding: .25em .625em;
      line-height: 1.75em;
      border-radius: .25em;
      text-transform: uppercase;
      transition: background-color .2s ease-out;
    }

    &.mbsc-button-standard {
      &.mbsc-hover {
        opacity: .8;
      }

      &.mbsc-active {
        opacity: .6;
      }
    }

    &.mbsc-button-outline {
      &.mbsc-hover {
        opacity: .8;
      }

      &.mbsc-active {
        opacity: 1;
      }
    }

    &.mbsc-button:disabled,
    &.mbsc-button.mbsc-disabled {
      opacity: .3;
    }

    &.mbsc-ltr {
      &.mbsc-button-icon-start {
        padding-right: .625em;
      }

      &.mbsc-button-icon-end {
        padding-left: .625em;
      }
    }

    &.mbsc-rtl {
      &.mbsc-button-icon-start {
        padding-left: .625em;
      }

      &.mbsc-button-icon-end {
        padding-right: .625em;
      }
    }

    .mbsc-button-group,
    .mbsc-button-group-justified {
      margin: .5em;
    }

    .mbsc-button-group-block {
      margin: .5em 1em;
    }
  }

  @include mbsc-mobiscroll-button('mobiscroll', $mbsc-mobiscroll-colors);
  @include mbsc-mobiscroll-button('mobiscroll-dark', $mbsc-mobiscroll-dark-colors);
}



