    height: 1.6em;
    line-height: 1.6em;
    margin: 0;
    padding: 0 .3em;
    font-size: .625em;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }

  .mbsc-schedule-event-all-day-background.mbsc-schedule-event-background {
    top: 0;
    bottom: 0;
  }

  /* Event Drag & drop */

  .mbsc-schedule-event-dragging {
    position: absolute;
    top: 0;
    z-index: 3;
  }

  .mbsc-schedule-event-inactive {
    opacity: .5;
  }

  .mbsc-schedule-event-hidden {
    visibility: hidden;
  }

  /* Event resize handles */

  .mbsc-schedule-event-resize {
    display: none;
    position: absolute;
    left: 0;
    right: 0;
    height: .25em;
    z-index: 1;
    cursor: ns-resize;
  }

  .mbsc-schedule-event-resize-start {
    top: 0;
  }

  .mbsc-schedule-event-resize-end {
    bottom: 0;
  }

  .mbsc-schedule-event-resize-start:after {
    top: -.25em;
    right: .5em;
  }

  .mbsc-schedule-event-resize-end:after {
    bottom: -.25em;
    left: .5em;
  }

  .mbsc-schedule-event-resize:after {
    content: '';
    position: absolute;
    margin: 1px 0;
    width: .5em;
    height: .5em;
    background: #fff;
    border-radius: 50%;
    border: 1px solid currentColor;
    box-sizing: border-box;
  }

  .mbsc-schedule-event-resize-start-touch:before,
  .mbsc-schedule-event-resize-end-touch:before {
    content: '';
    position: absolute;
    margin: 1px 0;
    width: 1.5em;
    height: 1em;
  }

  .mbsc-schedule-event-resize-start-touch:before {
    top: -.5em;
    right: 0;
  }

  .mbsc-schedule-event-resize-end-touch:before {
    bottom: -.5em;
    left: 0;
  }

  // .mbsc-schedule-event-active,
  .mbsc-schedule-event-dragging,
  .mbsc-schedule-event-hover {
    .mbsc-schedule-event-resize {
      display: block;
    }
  }


  /* Blocking and colored date styles */

  .mbsc-schedule-invalid,
  .mbsc-schedule-color {
    position: absolute;
    top: -.5em;
    bottom: -.5em;
    left: 0;
    right: 0;
    box-sizing: content-box;
    z-index: 0;
    overflow: hidden;
  }

  .mbsc-schedule-invalid-all-day,
  .mbsc-schedule-color-all-day {
    top: 0;
    bottom: 0;
    z-index: -1;
  }

  .mbsc-schedule-invalid-start,
  .mbsc-schedule-color-start {
    padding-top: .5em;
    margin-top: -.5em;
  }

  .mbsc-schedule-invalid-end,
  .mbsc-schedule-color-end {
    padding-bottom: .5em;
  }

  .mbsc-schedule-invalid-text,
  .mbsc-schedule-color-text {
    padding: .375em;
    font-size: .75em;
  }

  .mbsc-schedule-color-text {
    opacity: .8;
  }


  /* Time indicator */

  .mbsc-schedule-time-indicator {
    position: absolute;
    z-index: 2;
    pointer-events: none;
  }

  .mbsc-schedule-time-indicator-x {
    border-bottom: 1px solid;
  }

  .mbsc-schedule-time-indicator-x.mbsc-ltr {
    left: 4.25em;
    right: 0;
  }

  .mbsc-schedule-time-indicator-x.mbsc-rtl {
    right: 4.25em;
    left: 0;
  }

  .mbsc-schedule-time-indicator-y {
    border-left: 1px solid;
    top: 2em;
    bottom: 0;
    margin-top: -.25em;

    &.mbsc-ltr {
      margin-left: -1px;
    }

    &.mbsc-rtl {
      margin-right: -1px;
    }
  }

  .mbsc-schedule-time-indicator-day {
    position: absolute;
    width: 14.285714%;
  }

  .mbsc-schedule-time-indicator-day:before {
    content: '';
    position: absolute;
    width: .4375em;
    height: .4375em;
    margin-top: -.1875em;
    margin-left: -.1875em;
    margin-right: -.1875em;
    border-radius: 50%;
    z-index: 1;
  }

  .mbsc-schedule-time-indicator-time {
    position: absolute;
    top: -1em;
    font-size: .625em;
    line-height: 2em;
    margin-top: 1px;
    padding: 0 .8em;
    white-space: nowrap;
  }

  .mbsc-schedule-time-indicator-time-y {
    top: -1.8em;
    padding: 0 .5em;
    margin-left: -.5em;
    margin-right: -.5em;
    line-height: 1.6em;
  }

  .mbsc-schedule-time-indicator-time-x.mbsc-ltr,
  .mbsc-schedule-time-indicator-time-y.mbsc-rtl {
    right: 100%;
  }

  .mbsc-schedule-time-indicator-time-x.mbsc-rtl,
  .mbsc-schedule-time-indicator-time-y.mbsc-ltr {
    left: 100%;
  }

  /* Cursor indicator */

  .mbsc-schedule-cursor-time {
    position: absolute;
    z-index: 3;
    font-size: .625em;
    line-height: 2em;
    white-space: nowrap;
    box-sizing: border-box;
  }

  .mbsc-schedule-cursor-time-x {
    margin-top: -1em;

    &.mbsc-ltr {
      text-align: right;
    }

    &.mbsc-rtl {
      text-align: left;
    }
  }

  .mbsc-schedule-cursor-time-y {
    padding: 0 .5em;
    top: .9em;
  }

  @supports (overflow:clip) {

    .mbsc-schedule-event-inner {
      position: sticky;
      top: 0;
    }

  }

}


@if $mbsc-eventcalendar {
  .mbsc-schedule-wrapper {
    overflow: auto;
  }

  /* Resource groups */

  .mbsc-schedule-resource-group {
    // min-width: 16em;
    box-sizing: border-box;
    flex-basis: 16em;
  }

  .mbsc-schedule-resource-group:first-child:last-child {
    // min-width: 0;
    flex-basis: 0;
  }

  .mbsc-schedule-resource-group.mbsc-ltr {
    border-left-width: 2px;
    border-left-style: solid;
  }

  .mbsc-schedule-resource-group.mbsc-rtl {
    border-right-width: 2px;
    border-right-style: solid;
  }

  .mbsc-schedule-resource-group:first-child {
    border: 0;
  }

  /* Schedule header */

  .mbsc-schedule-date-header-text {
    line-height: 1.5em;
    text-align: center;
  }

  .mbsc-schedule-header {
    overflow: hidden;
  }

  .mbsc-schedule-all-day-group-wrapper,
  .mbsc-schedule-header-wrapper {
    min-width: 0;
    overflow: hidden;
  }

  .mbsc-schedule-header-item {
    display: block; // for angular component which displays in inline
    position: relative;
    text-align: center;
    line-height: 1.25em;
  }

  .mbsc-schedule-header-item.mbsc-hover {
    cursor: pointer;
  }

  .mbsc-schedule-header-dayname {
    font-size: .75em;
  }

  .mbsc-schedule-header-day {
    font-size: 1.125em;
  }

  /* Schedule column width for: header item, header resource, all-day item, schedule column  */

  .mbsc-schedule-col-width {
    box-sizing: border-box;
    // min-width: 0; // needed for the items equal width
    width: 2.5em;
  }

  .mbsc-schedule-wrapper-multi .mbsc-schedule-col-width {
    width: 6.25em;
  }

  /* Resource title */

  .mbsc-schedule-resource {
    overflow: hidden;
  }

  .mbsc-schedule-resource-title {
    padding: .5em;
    font-size: .875em;
    font-weight: 600;
    text-align: center;
  }

  /* Schedule all day event container */

  .mbsc-schedule-all-day-cont {
    position: relative;
  }

  .mbsc-schedule-all-day-wrapper {
    position: relative;
    max-height: 7.8125em;
    overflow-x: hidden;
    overflow-y: scroll;
  }

  .mbsc-schedule-all-day-wrapper-shadow:after {
    content: '';
    box-shadow: inset 0 0.5em 0.25em -0.5em rgba(48, 48, 48, 0.5);
    height: 4px;
    position: absolute;
    left: 0;
    right: 0;
    bottom: -4px;
    z-index: 4;
    pointer-events: none;
  }

  .mbsc-schedule-all-day-item {
    position: relative;
  }

  .mbsc-schedule-all-day-item:first-child,
  .mbsc-schedule-column:first-child {
    border: 0;
  }

  .mbsc-schedule-timezone-labels {
    position: absolute;
    bottom: 0;
    z-index: 1;
    font-weight: bold;
  }

  .mbsc-schedule-timezone-label {
    font-size: .75em;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .mbsc-schedule-time-indicator-tz {
    width: 5.666667em;
  }

  .mbsc-schedule-time-indicator-tz.mbsc-ltr,
  .mbsc-schedule-timezone-label.mbsc-ltr {
    text-align: right;
  }

  .mbsc-schedule-time-indicator-tz.mbsc-rtl,
  .mbsc-schedule-timezone-label.mbsc-rtl {
    text-align: left;
  }

  /* Time section */

  .mbsc-schedule-time-wrapper {
    position: relative;
    width: 4.25em;
    height: 3.125em;
    box-sizing: border-box;
    pointer-events: auto;
  }

  .mbsc-schedule-time {
    position: relative;
    line-height: 2em;
    top: -1em;
    font-size: .75em;
  }

  .mbsc-schedule-time-end {
    position: absolute;
    top: auto;
    bottom: -1em;
    left: 0;
    right: 0;
  }

  .mbsc-schedule-time-col.mbsc-ltr {
    text-align: right;
  }

  .mbsc-schedule-time-col.mbsc-rtl {
    text-align: left;
  }

  .mbsc-schedule-time-cont-pos {
    position: relative;
  }


  /* Grid */

  .mbsc-schedule-time-cont-inner {
    position: absolute;
    width: 100%;
    z-index: 2;
    pointer-events: none;
    height: 100%;
    box-sizing: border-box;
    padding: .5em 0;
  }

  .mbsc-schedule-column {
    padding: .5em 0;
  }

  .mbsc-schedule-column-inner {
    position: relative;
  }

  .mbsc-schedule-all-day-item,
  .mbsc-schedule-item {
    box-sizing: border-box;
  }

  .mbsc-schedule-item {
    height: 3.125em;
    position: relative;
    border-top: 1px solid;
  }

  .mbsc-schedule-item-last {
    border-bottom: 1px solid;
  }

  /* Event spacing */

  .mbsc-schedule-events {
    position: absolute;
    height: 100%;
  }

  .mbsc-schedule-events.mbsc-ltr {
    left: 0;
    right: .5em;
  }

  .mbsc-schedule-events.mbsc-rtl {
    right: 0;
    left: .5em;
  }

  /* All day events */

  .mbsc-schedule-event-all-day {
    .mbsc-schedule-event-resize {
      width: .25em;
      height: 100%;
      cursor: ew-resize;
    }

    .mbsc-schedule-event-resize:after {
      top: 50%;
      right: auto;
      left: auto;
      margin-top: -.25em;
    }

    .mbsc-schedule-event-resize-start.mbsc-ltr,
    .mbsc-schedule-event-resize-end.mbsc-rtl {
      right: auto;
    }

    .mbsc-schedule-event-resize-end.mbsc-ltr,
    .mbsc-schedule-event-resize-start.mbsc-rtl {
      left: auto;
    }

    .mbsc-schedule-event-resize-start.mbsc-ltr:after,
    .mbsc-schedule-event-resize-end.mbsc-rtl:after {
      right: -.25em;
    }

    .mbsc-schedule-event-resize-end.mbsc-ltr:after,
    .mbsc-schedule-event-resize-start.mbsc-rtl:after {
      left: -.25em;
    }

    /* Increase touch area for resize dots */

    .mbsc-schedule-event-resize-start-touch:before {
      height: 1.5em;
      top: 50%;
      left: -.75em;
      right: auto;
      margin-top: -.75em;
    }

    .mbsc-schedule-event-resize-end-touch:before {
      height: 1.5em;
      top: 50%;
      left: auto;
      right: -.75em;
      margin-top: -.75em;
    }
  }

  /* Large screen */

  .mbsc-calendar-width-md {

    .mbsc-schedule-event-title {
      font-size: 1.2em;
    }

    .mbsc-schedule-event-range {
      font-size: 1em;
    }

    .mbsc-schedule-event-all-day-title {
      font-size: 1em;
    }

    .mbsc-schedule-event-all-day-inner {
      font-size: .75em;
      height: 1.5em;
      line-height: 1.5em;
      padding: 0 .333334em;
    }
  }
}


@if $mbsc-eventcalendar {

  .mbsc-timeline {
