.item {
  /*   */
  --highlight-height: 0px;
  --padding-start: 0px;
  --padding-end: 0px;
  --inner-padding-start: 0px;
  --inner-padding-end: 0px;
  margin: 20px;
}

.invalid ion-input, .invalid ion-textarea {
  border: 1px solid var(--ion-color-danger) !important;
}

/* ios, md      . */
.label {
  color: var(--ion-text-color) !important;
  font-weight: bold;
  font-size: 14px !important;
  margin-bottom: 10px !important;
  transform: none !important;
  -webkit-transform: none !important;
}

.required:after {
  position: relative;
  bottom: 2px;
  content:" *";
  color: var(--ion-color-danger);
}

.submitButton {
  --border-radius: var(--dw-border-radius-4);
  --box-shadow: none;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 52px;
  margin-bottom: 20px;
  padding: 0 20px;
}
