/* 1RM   */
.absent {
  margin: 10px 20px 0 20px;
  padding: 25.5px 20px 25.5px 20px;
  color: white;
  text-align: center;
  background-color: var(--ion-color-primary);
  border-radius: var(--dw-border-radius-4);
}

.orm {
  margin: 10px 20px 0 20px;
  padding: 25.5px 20px 25.5px 20px;
  color: white;
  background-color: var(--ion-color-primary);
  border-radius: var(--dw-border-radius-4);
}

.record {
  float: right;
  position: relative;
  bottom: 6px;
  font-size: 24px; 
  font-weight: bold;
}

.hr {
  border-top: 5px solid var(--ion-color-step-100, #F8F8F8);
}