.header {
  color: var(--ion-text-color) !important;
  font-weight: bold;
  font-size: 14px;
  padding: 0px;
  margin: 0 20px 0px 20px;
}

.headerIosFont {
  font-size: 14px !important;
  margin-bottom: 8px;
}

.item {
  --padding-top: 10px;
  --padding-bottom: 10px;
  --padding-start: 16px;
  --padding-end: 16px;
  border: 1px solid var(--ion-color-step-200, #E0E0E0);
  border-radius: var(--dw-border-radius-4);
  margin-bottom: 10px;
}

.addButton {
  --border-radius: var(--dw-border-radius-4);
  --box-shadow: none;
  height: 48px;
}

.deleteButton {
  /*     (padding) . */
  --padding-start: 1px;
  --padding-end: 1px;
  --padding-top: 1px;
  --padding-bottom: 1px;
  --box-shadow: none;
  position: relative;
  top: 14px;
  width: 18px;
  height: 18px;
}
