.chips {
  white-space: nowrap;
  overflow-x: scroll;
}

.chips::-webkit-scrollbar {
  display: none;
}

.chip {
  --background: var(--ion-background-color);
  --color: var(--ion-color-step-800, #4F4F4F);
  border: 1px solid var(--ion-color-step-400, #F2F2F2);
}

.active {
  border: 1px solid var(--ion-color-primary) !important;
}
