.chips {
  margin: 5px 20px;
}

.list {
  margin-top: 5px;
  margin-bottom: 5px;
}

.item {
  padding: 21.5px 20px 21.5px 20px;
  margin-top: 5px;
  margin-bottom: 5px;
  border-radius: var(--dw-border-radius-4);
  background: var(--ion-color-step-200, #F8F8F8);
}

.icon {
  float: right;
  color: var(--ion-color-step-500, #E0E0E0);
}

.badge {
  float: right;
  position: relative;
  right: 25px;
  bottom: 2px;
  --padding-start: 10px;
  --padding-top: 5px;
  --padding-bottom: 5px;
  --padding-end: 10px;
}
