.dateInput {
  padding: 16px 20px 16px 20px;
  border: 1px solid var(--ion-color-step-200, #E0E0E0);
  border-radius: var(--dw-border-radius-4);
  width: 100%;
  /*     */
  justify-content: left;
}

/* CSS shadow-part */
.dateInput::part(native){
  background-color: var(--ion-background-color) !important;
  padding: 0px;
}

.saveButton {
  --border-radius: var(--dw-border-radius-4);
  --box-shadow: none;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 52px;
  margin-bottom: 20px;
  padding: 0 20px;
}
