.tableLinkDiv {
  margin: 20px 0 25px 0;
  text-align: center;
}

.tableLink {
  font-size: 14px;
  color: var(--ion-color-step-400, #BDBDBD);
  background-color: var(--ion-background-color);
  border-bottom: 0.5px solid var(--ion-color-step-400, #BDBDBD);
  padding-bottom: 5px;
}

.tableLinkIcon {
  margin-left: 5px;
  color: var(--ion-color-step-400, #BDBDBD);
}

.modal {
  --border-radius: var(--dw-border-radius-16);
  --height: 98%;
  --width: 98%;
}

.row {
  padding: 5px 15px 0 15px;
}

.para {
  /*      . */
  line-height: 24px !important;
}

.repText {
  font-size: 18px;
  color: var(--ion-text-color);
}

.rateText {
  position: absolute;
  right: 100px;
  color: var(--ion-color-step-400, #BDBDBD);
  vertical-align: middle;
}

.weightSpan {
  float: right
}

.weightText {
  padding-right: 5px;
  font-size: 24px;
  color: var(--ion-color-primary);
}

.weightUnitText {
  color: var(--ion-text-color);
}
