/*      */
.titleSentDiv {
  margin: 190px 80px 0 80px;
  text-align: center;
  font-size: 20px;
}

.titleSent {
  margin: 0;
  line-height: 28px;
}

.infoDiv {
  margin: 0 20px 0 20px;
  padding: 12px 20px 12px 20px;
  border-radius: 4px;
  background-color: #E7FFF3;
}

.infoTitleDiv {
  margin: 0px;
  color: var(--ion-color-primary);
  font-size: 14px;
}

.infoIcon {
  position: relative;
  top: 2px;
  padding-right: 10px;
}

.infoMessageDiv {
  margin: 5px 0 0 0;
  padding-left: 24px;
  font-size: 12px;
  color: var(--ion-color-step-400, #828282);
}

.backButton {
  margin: 33px 20px 0 20px;
  height: 50px;
  color: var(--ion-color-step-400, #BDBDBD);
}

/*      */
.titleDiv {
  margin-top: 120px;
  text-align: center;
}

.title {
  font-size: 40px;
}

.subInfo {
  font-size: 14px;
  color: var(--ion-color-step-400, #BDBDBD);
}

.formDiv {
  width: 100%;
  position: absolute;
  bottom: 0;
  margin-bottom: 70px;
}

.form {
  padding: 0;
}

.item {
  margin: 0 20px 10px 20px;
}

.startButton {
  margin: 0 20px 0 20px;
  height: 50px;
  --box-shadow: none;
  --border-radius: var(--dw-border-radius-4);
}

.startWithoutSignInButton {
  margin: 60px 20px 0 20px;
  height: 50px;
  color: var(--ion-color-step-400, #BDBDBD);
}
