.calendarDiv {
  margin: 0 20px 0 20px;
}

/*   . */
.calendarHeaderDiv {
  position: relative;
  width: 100%;
}

.calendarPrev {
  float: left;
}

.calendarNav {
  text-align: center;
}

.calendarNext {
  float: right;
}

.calendarMark {
  background: var(--ion-color-primary) !important;
  width: 5px !important;
  height: 5px !important;
}

.listDiv {
  border-top: 1px solid var(--ion-color-step-150, #dbdbdb);
  position: relative;
  /* top: -15px; */
}

.emptyDiv {
  margin-top: 80px;
}
